export default (state=[], action) => {
  const createId = () => {
    return state.length > 0
      ? state.sort((a, b) => a.id - b.id).unshift() + 1
      : 1;
  };

  const reorder = (list, startIndex, endIndex) => {
    const result = Array.from(list);
    const [removed] = result.splice(startIndex, 1);
    result.splice(endIndex, 0, removed);
  
    return result;
  };  

  switch (action.type) {
    case "ADD":
      return state.length>0?[
        ...state,
        { id: createId(), task: action.task, completed: false }
      ]:[ { id: createId(), task: action.task, completed: false }];
    case "REMOVE":
      return state.filter(todo => todo.id !== action.id);
    case "TOGGLE":
      return state.map(todo =>
        todo.id === action.id ? { ...todo, completed: !todo.completed } : todo
      );
    case "EDIT":
      return state.map(todo =>
        todo.id === action.id ? { ...todo, task: action.task } : todo
      );
    case "DragEnd": {
      // dropped outside the list
      if (!action.result.destination) {
        return;
      }

      const items = reorder(
        state,
        action.result.source.index,
        action.result.destination.index
      );
      return items;
    }
    default:
      return state;
  }
};

